body{
    background-color: rgb(220,220,220);
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.image{
    width: 370px;
    margin-top: 50px;
    margin-bottom: 30px;;
}

@media (max-width: 770px){
    .container {
        margin: 0 10%;
    }

    .image{
        width: 100%;
    }
}